import React, { Component } from 'react';
import image from '../LogoMakr_1xAkjh.png';
import {Link} from 'react-router-dom'

class Header extends Component {
  render() {
    return (
      <React.Fragment>    
      <header style={{backgroundColor:'#008B8B',height:'15vh',display:'flex',padding:'15px'}}>
          <div style={{display:'flex',width:'120vh'}}>
            <div><img  style={{width:'15vh'}} src={image}></img></div>
            <div style={{margin:"8px 0px 0px -14px"}}><a  style={{fontSize:'30px',color:'midnightblue'}}>HASHTAG MEDIA</a></div>  
         </div>
        <Link to="/signin"><div style={{padding:'15px'}}>Sign In</div></Link> 
        <Link to="/"><div style={{padding:'15px'}}>Login</div></Link> 
        <Link to="/"> <div style={{padding:'15px'}}>News</div></Link> 
        <Link to="/"><div style={{padding:'15px'}}>News</div></Link> 
        <div style={{padding: '15px'}}><input type="text" placeholder="enter the search title"></input></div>
      </header>
      </React.Fragment> 

    );
  }
}

export default Header;








